import ReactDOM from 'react-dom/client';
// import './index.css';
import HelloApp from './HelloApp';
import React from 'react';

export class Solution {
  divHost;
  constructor(divHost: HTMLDivElement) {
    const div = document.createElement('div');
    divHost.appendChild(div);
    this.divHost = divHost;

    const root = ReactDOM.createRoot(div);

    root.render(
      // <React.StrictMode>
      // <HelleApp /> syntax sugar
      React.createElement(HelloApp, {})
      // </React.StrictMode>
    );
  }
  destroy() {
    while (this.divHost.lastElementChild) {
      this.divHost.removeChild(this.divHost.lastElementChild);
    }
  }
}

